<template>
  <div id="headerbar-block">
    <ul class="tag-container">
      <li
        v-for="(tag,index) in tagList"
        class="header-tag"
        :key="index"
      >
        <router-link tag="div" :to="tag.meta.path" class="tagname">{{tag.meta.title}}</router-link>
        <div
          @click="close_tag(index)"
          @mouseenter="curHover_index = index"
          @mouseleave="curHover_index = -1"
          class="close-btn"
        >
          <svg class="icon headerbar-icon" aria-hidden="true">
            <use :xlink:href="(index === curHover_index)?'#myinconquxiao':'#myinconquxiao1'" />
          </svg>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "headerbar",
  data() {
    return {
      tagList: [],
      curHover_index: -1
    };
  },
  watch: {
    "$store.state.curRoute"(value) {
      setTimeout(() => {
        value.url = this.$store.state.curUrl;
        this.tagList.push(value);
        this.tagList = Array.from(new Set(this.tagList));
      }, 100);
    }
  },
  methods: {
    close_tag(index) {
      this.tagList.splice(index, 1);
      this.curHover_index = -1;
    }
  }
};
</script>

<style scoped>
#headerbar-block {
  width: 100%;
  height: 60px;
  border: 1px solid rgb(184, 184, 184);
  border-left: none;
}
.tag-container {
  display: flex;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.actove-tag{
  border: 2px solid rgb(146, 133, 133);
}
.tag-container :hover {
  /* background-color: rgb(122, 122, 219);
  color: white;
  border: none; */
  /* border: 2px solid rgb(146, 133, 133); */
}
.header-tag {
  display: flex;
  flex-shrink: 1;
  height: 35px;
  line-height: 35px;
  padding: 0 0px 0 6px;
  margin-left: 5px;
  margin-top: 8px;
  border: 1px solid rgb(146, 133, 133);
  border-radius: 5px;
  cursor: pointer;
}

.tagname {
  flex: 9;
  height: 35px;
  font-size: 12px;
  text-align: left;
  border: none !important;
}
.tagname.router-link-active{color:rgb(233, 44, 240); font-weight: bold;}
.close-btn {
  flex: 1;
  height: 35px;
  font-size: 8px;
  padding-left: 3px;
  padding-right: 3px;
  line-height: 36px;
  border: none !important;
}
.headerbar-icon {
  font-size: 15px;
  margin: 0 auto;
  border: none !important;
}
</style>