
import Mock from 'mockjs'

const Random = Mock.Random

var TokanData = function() {
    let _data = []

    let newList = { // 详细 规则 参照mockjs官网
      token: Random.string(5)
    }
      _data.push(newList)
  return {_data: _data}
}
var newsData = function() {
    let _data = []
    for (let i = 0; i < 20; i++) {
    let newList = { // 详细 规则 参照mockjs官网
      title: Random.csentence(5), //  Random.csentence( min, max )
    }
      _data.push(newList)
  }
  return {_data: _data}
}
// url为要拦截的请求地址  请求方式  请求数据（规则） （此处api会被mockjs拦截）
Mock.mock('http://myapp/login', 'post', TokanData)
Mock.mock('http://route.showapi.com/news', 'get', newsData)