<template>
  <div id="sidebar-item">
    <label v-for="navItem in routerlist">
      <!-- 当判断无子路由 -->
      <el-menu-item
        @click.native="handleSwitchPage(navItem)"
        v-if="!navItem.meta.mulit"
        :index="navItem.meta.index"
      >
        <svg v-if="navItem.meta.order" class="icon siderBar-icon" aria-hidden="true">
          <use :xlink:href="navItem.meta.icon" />
        </svg>
        <span slot="title">{{navItem.meta.index}}. {{navItem.meta.title}}</span>
      </el-menu-item>
      <!-- 当判断有子路由时 -->
      <el-submenu
        v-else
        :index="navItem.meta.index"
        :class="[isCollapse?'hidden_menuq':'']" 
      >
        <template slot="title" >
          <svg v-if="navItem.meta.order" class="icon siderBar-icon"  aria-hidden="true">
            <use  :xlink:href="navItem.meta.icon" />
          </svg>
          <span :class="[isCollapse?'hidden_menu':'show_menu']"  >{{navItem.meta.index}}. {{navItem.meta.title}}</span>
        </template>
        <!-- 递归渲染侧边栏 -->
        <siderbar-item :routerlist="navItem.children"></siderbar-item>
      </el-submenu>
    </label>
  </div>
</template>

<script>
export default {
  name: "siderbarItem",
  data() {
    return {};
  },
  props: {
    routerlist: {
      type: Array
    },
    isCollapse: {
      type: Boolean
    }
  },
  methods: {
    handleSwitchPage(router){
      if(router.path === "/login"){
         localStorage.clear();
         window.location.reload();
      }else{
        this.$store.commit("chcurRoute",router);
        this.$router.push(router.meta.path);
      }
    }
  }
};
</script>

<style scoped>
.siderBar-icon {
  color: rgb(255, 255, 255);
}
.hidden_menu{
  display: none;
}
.hidden_menuq{
  color: #483D8B !important;
}
.show_menu{
  display: inline;
}
</style>