import Vue from 'vue'
import App from './App.vue'
import router from './router/index'
import store from './store'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import mock from './mock'


import './assets/iconfont/iconfont.js'
import './assets/iconfont/style.css'



import dynamicAddRoutes from './dynamicRouter'

import echarts from 'echarts'
Vue.prototype.$echarts = echarts


Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  let token = localStorage.getItem('token'); //获取用户token与权限
  let permissions = localStorage.getItem('permissions')

  if (token != null) {  //当前无token时
    if (store.state.permissions.length === 0) {  //当界面刷新之后,store的数据清除
      dynamicAddRoutes(permissions) //重新根据本地存储的用户权限配置动态路由
      next({ ...to, replace: true }); //进入上一个刷新的页面
    } else {
      next()
    }
  } else {
    if (to.path === '/login') {
      next()
    } else {
      next('/login')
    }
  }
});


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')