<template>
  <div id="sidebar-page">
    <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      :collapse="isCollapse"
      background-color="#483D8B"
      text-color="#F8F8FF"
      active-text-color="#F8F8FF"
    >
      <el-menu-item @click="isCollapse = !isCollapse">
        <svg
          :class="[isCollapse?'collapsed_icon':'collapse_icon']"
          class="icon siderBar-icon"
          aria-hidden="true"
        >
          <use :xlink:href="isCollapse?'#myinconqianjin':'#myinconhoutui'" />
        </svg>
      </el-menu-item>
      <siderbar-item :routerlist="this.routerlist" :isCollapse="this.isCollapse"></siderbar-item>
    </el-menu>
  </div>
</template>

<script>
import siderbarItem from "./SidebarItem";
export default {
  components: {
    siderbarItem
  },
  data() {
    return {
      isCollapse: false,
      routerlist: []
    };
  },
  mounted() {
    let temRouter = [];
    this.$router.options.routes.forEach(router => {
      //判断分类选项是否有多项子路由
      if (router.hasOwnProperty("children")) {
        temRouter.push(router);
      }
    });
    temRouter.sort((a, b) => {  //根据路由配置的order来进行排序
      if (a.meta.order < b.meta.order) return -1;
    });
    this.formatting(temRouter);
  },
  methods: {
    formatting(routerlist, before_index,before_path) {
      //格式化路由的index函数  采用递归的方法将每个路由添加index
      //并且更新所有子路由的path到meta标签,以供直接跳转使用
      let CurIndex = "1";

      if (before_index != null) {
        //当已经开始递归
        if (routerlist.meta.mulit) {
          //当前路由存在子路由时
          routerlist.children.forEach(route => {
            //遍历当前路由
            let temPath = before_path +'/'+ route.path
            route.meta.path = temPath;
            route.meta.index = before_index + "-" + CurIndex; //给当前路由的所有子路由添加index
            this.formatting(route, before_index + "-" + CurIndex,temPath); //给当前子路由的下一层路由添加index(有则添加 无则无效)
            CurIndex++; //路由序号增加
            CurIndex = CurIndex.toString();
          });
        }
      } else {
        routerlist.forEach(route => {
          route.meta.index = CurIndex;
          route.meta.path = route.path;
          if (route.meta.mulit) {
            //当前路由存在子路由时
            this.formatting(route,CurIndex,route.path); //传入父序号递归调用
          }
          CurIndex++; //自增后便会为 string类型
          CurIndex = CurIndex.toString();
        });
      }

      this.routerlist = routerlist;
      return;
    }
  }
};
</script>

<style scoped>
#sidebar-page {
  height: 100%;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 250px;
  min-height: 400px;
}
#sidebar-page >>> .el-menu-item:hover {
  background-color: rgb(70, 101, 187) !important;
}
#sidebar-page >>> .el-menu-vertical-demo {
  height: 100%;
}
.collapse_icon {
  animation: iconMove .5s forwards;
}

@keyframes iconMove {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: 90%;
  }
}
.collapsed_icon {
  
}
</style>