<template>
  <div id="mainView">
    <header-bar></header-bar>
    <keep-alive>
      <router-view :key="key"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import headerBar from "./components/HeaderBar";
export default {
  components: {
    headerBar
  },
  computed: {
    key() {
      // 只要保证 key 唯一性就可以了，保证不同页面的 key 不相同
      return this.$route.fullPath;
    }
  }
};
</script>

<style>
#mainView {
  flex-grow: 1;
  overflow: scroll;
}
</style>