import { asyncRouter } from "@/router/index";
import router from './router/index'
import store from './store'

export default function dynamicAddRoutes(Permissions){

    let temRouter = [];  //根据用户权限配置动态路由
    asyncRouter.forEach(router => {    //筛选出符合当前用户权限的路由
        if (router.meta.role[0] === Permissions) {
        temRouter.push(router);
        }
    }); 
    router.options.routes.push(...temRouter);
    router.addRoutes(temRouter);
    store.commit("chPermissions", Permissions);  //重新配置store当前用户权限
}