import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from '@/layout'
/*
router配置:
    meta属性:
    title ---- 标题
    mulit ---- 是否有嵌套子路由
    icon  ---- 图标名
    order ---- 当前标题的排序

    多层路由只需多写 <router-view></router-view>

 */

export const constantRoutes = [{
        path: '/login',
        component: () => import('@/views/login')
    },
    {
        path: '',
        redirect: '/general'
    },
    {
        path: '/general',
        component: Layout,
        meta: {
            title: '通用',
            mulit: false,
            order:1,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            component: () => import('@/views/general'),
            name: 'General',
        }]
    },
    {
        path: '/login',
        meta: {
            title: '登出',
            mulit: false,
            order:5,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            name: 'Login',

        }]
    },
]

const createRouter = () => new Router({
    // mode: 'history', // require service support
    scrollBehavior: () => ({
        y: 0
    }),
    routes: constantRoutes
})

const router = createRouter()

export function resetRouter() {
    const newRouter = createRouter()
    router.matcher = newRouter.matcher // reset router
}

export const asyncRouter = [{
        path: '/info',
        component: Layout,
        meta: {
            title: '信息',
            role: ['admin'],
            mulit: false,
            order:3,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            component: () => import('@/views/info'),
            name: 'Info',

        }],

    },
    {
        path: '/a',
        component: Layout,
        meta: {
            title: '管理员页面',
            role: ['admin'],
            mulit: false,
            order:4,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            component: () => import('@/views/info'),
            name: 'Info',

        }],

    },
    {
        path: '/b',
        component: Layout,
        meta: {
            title: '游客页面',
            role: ['visitor'],
            mulit: false,
            order:3,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            component: () => import('@/views/info'),
            name: 'Info',

        }],

    },
    {
        path: '/news',
        component: Layout,
        meta: {
            title: '新闻',
            role: ['visitor'],
            mulit: false,
            order:4,
            icon:'#myinconqianjin'
        },
        children: [{
            path: '',
            component: () => import('@/views/news'),
            name: 'News',

        }],
    },
    {
        path: '/father',
        component: Layout,
        redirect: '/father/son',
        name: 'Father',
        meta: {
            title: '父菜单',
            role: ['admin'],
            mulit: true,
            order:2,
            icon:'#myinconqianjin'
        },
        children: [{
            path: 'son',
            component: () => import('@/views/son'),
            name: 'Son',
            meta: {
                title: '子菜单',
                role: ['admin'],
                mulit: true,
                icon:'#myinconqianjin'
            },
            children: [{
                path: 'grandson',
                component: () => import('@/views/grandson'),
                name: 'Grandson',
                meta: {
                    title: '孙子菜单',
                    role: ['admin'],
                    mulit: true,
                    icon:'#myinconqianjin'
                },
                children:[
                    {
                        path: 'brother',
                        component: () => import('@/views/info'),
                        name: 'brother',
                        meta: {
                            title: '臭弟弟菜单',
                            role: ['admin'],
                            mulit: false,
                            icon:'#myinconqianjin'
                        },
                    }
                ]
            }]
        }],
    }

];


export default router