import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
     permissions:[],
     curUrl:'',
     curRoute:{},
     isItem_click:false
  },
  mutations: {
    chPermissions(state,value){
      state.permissions = value;
    },
    chcurUrl(state,value){
      state.curUrl = value;
    },
    chcurRoute(state,value){
      state.curRoute = value;
    },
    chisItem_click(state,value){
      state. isItem_click = value;
    }
  },
  actions: {

  }
})
